import React from "react";
import { Link } from "react-router-dom";

const Header = () => {
	return (
    <React.Fragment>
    <nav className="navbar navbar-expand-md navbar-light bg-light sticky-top">
    <div className="container-fluid">
    <Link to="/My-Portfolio"class="navbar-brand" href="#">kaleido's portfolio</Link>
      <button className="navbar-toggler" type="button" data-toggle="collapse"
      data-target="#navbarResponsive">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarResponsive">  
        <ul className="navbar-nav ml-auto">
        
          <li className="nav-item">
            <Link to="/My-Portfolio"className="nav-link">Toppage</Link>
          </li>
          <li className="nav-item">
            <Link to="/My-Portfolio/profile" className="nav-link">Profile</Link>
          </li>
          <li className="nav-item">
            <Link to="/My-Portfolio/skill" className="nav-link">Skill</Link>
          </li>
          <li className="nav-item">
            <Link to="/My-Portfolio/framework" className="nav-link">Framework</Link>
          </li>
          </ul>
        </div>
    </div>
    </nav>
    
    </React.Fragment>
	);
};

export default Header;
